import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/home/Home'
import Register from '@/pages/auth/components/Register'
import Login from '@/pages/auth/components/Login'
import ProjectList from '@/pages/project/ProjectList'
import DataBase from '@/pages/project/DataBase'
import ProjectDetail from '@/pages/project/ProjectDetail'
import DebugTalk from '@/pages/httprunner/DebugTalk'
import RecordApi from '@/pages/fastrunner/api/RecordApi'
import AutoTest from '@/pages/fastrunner/case/AutoTest'
import GlobalEnv from '@/pages/variables/GlobalEnv'
import ReportList from '@/pages/reports/ReportList'
import RecordConfig from '@/pages/fastrunner/config/RecordConfig'
import Tasks from '@/pages/task/Tasks'

Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/fastrunner/register',
            name: 'Register',
            component: Register,
            meta: {
                title: ''
            }
        }, {
            path: '/fastrunner/login',
            name: 'Login',
            component: Login,
            meta: {
                title: ''
            }
        }, {

            path: '/fastrunner',
            name: 'Index',
            component: Home,
            meta: {
                title: '',
                requireAuth: true
            },
            children: [
                {
                    name: 'ProjectList',
                    path: 'project_list',
                    component: ProjectList,
                    meta: {
                        title: '',
                        requireAuth: true,
                    }
                },
                {
                    name: 'ProjectDetail',
                    path: 'project/:id/dashbord',
                    component: ProjectDetail,
                    meta: {
                        title: '',
                        requireAuth: true,
                    }

                },
                {
                    name: 'DebugTalk',
                    path: 'debugtalk/:id',
                    component: DebugTalk,
                    meta: {
                        title: '',
                        requireAuth: true,
                    }

                },
                {
                    name: 'RecordApi',
                    path: 'api_record/:id',
                    component: RecordApi,
                    meta: {
                        title: '',
                        requireAuth: true
                    }

                },
                {
                    name: 'AutoTest',
                    path: 'auto_test/:id',
                    component: AutoTest,
                    meta: {
                        title: '',
                        requireAuth: true
                    }

                },
                {
                    name: 'RecordConfig',
                    path: 'record_config/:id',
                    component: RecordConfig,
                    meta: {
                        title: '',
                        requireAuth: true
                    }

                },
                {
                    name: 'GlobalEnv',
                    path: 'global_env/:id',
                    component: GlobalEnv,
                    meta: {
                        title: '',
                        requireAuth: true
                    }

                },
                {
                    name: 'Reports',
                    path: 'reports/:id',
                    component: ReportList,
                    meta: {
                        title: '',
                        requireAuth: true
                    }

                },
                {
                    name: 'Task',
                    path: 'tasks/:id',
                    component: Tasks,
                    meta: {
                        title: '',
                        requireAuth: true
                    }

                },
                {
                    name: 'Pressure',
                    path: 'pressure/:id',
                    component: RecordConfig,
                    meta: {
                        title: '',
                        requireAuth: true
                    }

                },
                {
                    name: 'DataBase',
                    path: 'database',
                    component: DataBase,
                    meta: {
                        title: '',
                        requireAuth: true
                    }

                }
            ]
        },

    ]
})




